import {AbstractFilter} from './abstract_filter'

export class Exists extends AbstractFilter {
    private field : string;

    constructor(field: string) {
        super();
        this.field = field;
    }

    public static make(field: string) : Exists {
        return new Exists(field);
    }

    public toJSON() {
        return {exists: {field: this.field}};
    }
}